import React from 'react'
import './App.css'

class App extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      isLoaded: false,
      items: []
    }
  }

  componentDidMount () {
    window
      .fetch('http://api.open-notify.org/iss-now.json')
      .then(res => res.json())
      .then(json => {
        console.log(json.rates)
        this.setState({
          isLoaded: true,
          ans: json.message,
          longitude: json.iss_position.longitude,
          latitude: json.iss_position.latitude
        })
      })
  }

  render () {
    const { ans, isLoaded, longitude, latitude } = this.state
    console.log(this.state)
    if (!isLoaded) {
      return <div><h2>NOWSERCHIG....</h2></div>
    }
    return (
      <div>
        <h1>{ans}!</h1>
        <p>現在の国際宇宙ステーションの位置は</p>
        <p>緯度:{latitude}</p>
        <p>経度:{longitude}</p>
      </div>
    )
  }
}

export default App
